section {
    width: 100vw;
    height: 100vh;
}
.mood {
    position: relative;
    width: 100vw;
    height: 100vh;
    color: white;
    display: flex;
    font-family: "Megrim", cursive;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    transition: all ease 1s 1s;
    overflow: hidden;
}
.mood-div {
    height: 70%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    justify-content: center;
    gap: 5vw;
    z-index: 1;
}
.mood-card {
    height: 60%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: all 0.5s 0.1s;
}
.mood-image {
    width: 70%;
}
.mood-title {
    font-size: 5rem;
    line-height: 1.5;
    width: 100%;
    text-align: center;
    z-index: 1;
    background: linear-gradient(to top, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.5));
}
.mood-name {
    font-size: 2.5rem;
    text-align: center;
    width: 70%;
    background: linear-gradient(to top, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.5));
}

.mood-foot-cred {
    font-size: 2rem;
    width: 90%;
    text-align: right;
    color: white;
}
.mood-card:hover {
    transform: translateY(2rem);
    transition: all 0.5s 0.1s;
}
.mood-foot {
    width: 100%;
    display: flex;
    justify-content: space-between;
    z-index: 1;
}
.music-box {
    display: flex;
    position: absolute;
    bottom: 2rem;
    left: 2rem;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    margin-left: 3rem;
    width: 15rem;
    z-index: 1;
}
.select-box {
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 5px;
    border: 2px solid rgb(5, 238, 219);
}
.music-text {
    font-size: 4rem;
    background: linear-gradient(to top, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.5));
}
.frame {
    height: 100vh;
    width: 100vw;
    position: absolute;
    z-index: 0;
    /* transform: scale(1.2); */
}
